<template>
    <div class="review-ogct">
        <button class="back-button" @click="$router.back()">
            <img class="back-button-arrow" src="@/assets/newImages/black_arrow.png"/>
            <p class="back-button-text">{{$localizeFilter('our_promotions')}}</p>
        </button>

        <div class="container">
             <div class="window light">
                <p class="window-title bold-blue">{{$localizeFilter('review_ogct')}}</p>
                <div class="window-subtitle">{{$localizeFilter('date_from_to', [$dateTimeFilter(startDate, 'custom', 'dd.mm.yyyy'), $dateTimeFilter(endDate, 'custom', 'dd.mm.yyyy')])}}</div>
                <div class="main-text" v-html="$localizeFilter('review_ogct_main_text')"></div>

                <p class="sub-title bold-blue">{{$localizeFilter('terms_of_action')}}</p>

                <p class="window-text" v-html="$localizeFilter('review_ogct_window_text')"></p>

                <div class="copy-text-window">
                  <div class="copy-text-window-content">
                    <div class="link-row">
                      <p class="link-text">{{links.site.text}}</p>
                      <p class="link-link" @click="window.open(links.site.link)">{{links.site.link}}</p>
                    </div>
                    <div class="link-row">
                      <p class="link-text">{{links.youtube.text}}</p>
                      <p class="link-link" @click="window.open(links.youtube.link)">{{links.youtube.link}}</p>
                    </div>
                    <div class="link-row">
                      <p class="link-text">{{links.telegramChannel.text}}</p>
                      <p class="link-link" @click="window.open(links.telegramChannel.link)">{{links.telegramChannel.link}}</p>
                    </div>
                    <div class="link-row">
                      <p class="link-text">{{links.telegramChat.text}}</p>
                      <p class="link-link" @click="window.open(links.telegramChat.link)">{{links.telegramChat.link}}</p>
                    </div>
                  </div>
                  <button class="basic-button copy-button" @click="$utils.copy(allLinksText)">{{$localizeFilter('copy')}}</button>
                </div>

                <div class="footer">
                  <div class="already-joined" v-if="this.dataSent">{{$localizeFilter('review_ogct_send_successful')}}</div>
                  <!-- <div class="already-finished-accepting_new_contestants" v-else>{{$localizeFilter('review_ogct_finished_accepting_new_applications')}}</div> -->
                  <button class="basic-button join-button" @click="joinOffer" v-else>{{$localizeFilter('participate')}}</button>
                </div>
             </div>
         </div>
         <!-- <Modal v-if="showModal" :type="'bestOGCTReview'" @closeModal="closeModal"/> -->
         <ReviewOGCTModal v-if="showModal" @closeModal="closeModal"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
// import Modal from "@/components/modals/Modal.vue"
import ReviewOGCTModal from '../components/newModals/ReviewOGCTModal.vue';

export default defineComponent({
    components: {
        // Modal,
        ReviewOGCTModal, 
    },
    props: ['startDate', 'endDate'],
    methods: {
      async closeModal() {
        this.showModal = false;
        this.dataSent = await this.$store.dispatch('getSpecialOfferData', {offerName: 'bestOGCTReview'})
      },
      joinOffer() {
        this.showModal = true;
      }
    },
    setup() {
        
    },
    data: () => ({
      showModal: false,
      links: {
        site: {text: localizeFilter('our_site'), link: 'https://capitalwhale.io/#/'},
        youtube: {text: 'YouTube', link: 'https://www.youtube.com/channel/UC7to7Nai9043raVVvKYm6Hg'},
        telegramChannel: {text: localizeFilter('our_telegram_channel'), link: 'https://t.me/Capital_Whale'},
        telegramChat: {text: localizeFilter('our_official_telegram_chat'), link: 'https://t.me/+FYZCWBFFhuswYmM6'}
      },
      dataSent: null
    }),
    computed: {
      allLinksText() {
        let res = ''
        Object.entries(this.links).forEach(entry => {
          const [key,value] = entry
          res = res + value.text + ' ' + value.link + '\n'
        })    
        res = res.substring(0, res.length - 1);
        return res  
      }
    },
    async mounted() {
       try {
            this.dataSent = await this.$store.dispatch('getSpecialOfferData', {offerName: 'bestOGCTReview'})
        }
        catch (e) {
          this.$error(e)
        }
    }
})
</script>

