                        <input class="input-field" type="text" v-model="link.text" :placeholder="$localizeFilter('review_ogct_link')"><div class="button-link-wrapper">
                        <!-- <button class="basic-button add-link-button" v-if="idx == links.length - 1 && links.length < 4" @click="addLink">{{$localizeFilter('review_ogct_add_link')}}</button> -->
                        </div>
                    </div>
                </div>

                <div class="coin-block" :style="'margin-bottom: 14px;'">
