                        <img src="@/assets/newImages/coins/icons/usdt_icon_transparent.png" alt="">
                        <div class="coin">USDT TRC-20</div>
                    </div>
                    <div class="content">{{$localizeFilter('review_ogct_will_go_to_bonus_account')}}</div>
                </div>
                <!-- <div class="coin-block last">
