                </div> -->


                <!-- <input id="address" class="custom-input wallet" :placeholder="$localizeFilter('wallet_number', ['Ethereum'])" type="text" v-model.trim="address" :class="{ invalid: v$.address.$error }"  v-maska="{tokens: { 'H': { pattern: /[0-9a-fA-F]/, uppercase: true }}, mask: '0xHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHH'}"/> -->

                <!-- <div class="links">
